.navbar {
    position: fixed;
    bottom: 0px;
    width: 100%;
    background-color: #303030;
    color: white;
    display: flex;
    justify-content: center;
    height: 7vh;
    align-items: center;
    z-index: 4;
}

.navbar-items {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    justify-items: center;
}

.navbar-item {
    margin-left: 1em;
    margin-right: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
}
